<template>
  <div class="simulation-box">
    <div class="attribute-box">
      <div class="level-a">
        <p
          v-for="item in ATTRIBUTE_NAV"
          @click="allocation(item.typeName, item.tips)"
          :key="item.typeName"
          :class="{ active: aTypeName === item.typeName }"
        >
          {{ item.typeText }}
        </p>
      </div>
    </div>
    <div class="attribute-bottom">
      <!-- 左侧 -->
      <div class="data-set-key">
        <div class="set-key-title">
          <p class="a-type-name">{{ aTypeName }}</p>
          <p class="a-type-name fr" @click="openDetails = true ">
            {{ aTypeTips }}
          </p>
        </div>
        <div class="set-key-list">
          <ul>
            <li v-for="item in objList" :key="item.name">
              {{ item.name }} : {{ item.type }}
            </li>
          </ul>
        </div>
        <p class="generate-btn" v-if="objList.length" @click="generateData">
          生成数据
        </p>
      </div>
      <!-- 右侧 -->
      <div class="data-show">
        <pre class="data-text">{{ outputData }}</pre>
      </div>
      <!-- 设置详情弹窗 -->
      <div class="set-details-zzc" v-show="openDetails">
        <div class="set-details" ref="detailsbox">
          <div class="set-details-top-box" @mousedown.self="clickDetails">
            模型配置
            <p
              class="iconfont icon-sousuo set-details-off"
              @click="openDetails = false"
            ></p>
          </div>
          <set-details @addAttribute="addAttribute" :aTypeTips="aTypeTips"></set-details>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { ATTRIBUTE_NAV, ATTRIBUTE_TYPE } from './const.js';
import setDetails from './setDetails.vue';
import { randomString } from './publicFn.js';
export default {
  props: {},
  name: 'simulation',
  components: { setDetails },
  setup() {
    const data = reactive({
      detailsbox: null,
      openDetails: false,
      aTypeName: '', //类型名字
      aTypeTips: '', //对应类型添加提示
      ATTRIBUTE_NAV,
      objList: [], //保存所有属性
      outputData: '',
      json: [],
      addAttributeType:''
    });
    onMounted(() => {});
    // 移动元素
    const clickDetails = (moveInEle) => {
      const ele = data.detailsbox; //要移动的元素
      document.onmousemove = function (e) {
        const eleTop = e.clientY - moveInEle.offsetY;
        const eleLeft = e.clientX - moveInEle.offsetX;
        const noBottom = document.body.offsetHeight - ele.offsetHeight; //底部限制
        const noRight = document.body.offsetWidth - ele.offsetWidth; //右侧显示
        const topPx = eleTop < 0 ? 0 : eleTop > noBottom ? noBottom : eleTop;
        const leftPx = eleLeft < 0 ? 0 : eleLeft > noRight ? noRight : eleLeft;
        ele.style.transform= 'translate(0%)';
        ele.style.left = leftPx + 'px';
        ele.style.top = topPx + 'px';
      };
      document.onmouseup = function (e) {
        console.log('抬起');
        document.onmousemove = null;
        document.onmouseup = null;
      };
    };
    // 首次创建数据
    const allocation = (type, tips) => {
      data.aTypeName = type;
      data.aTypeTips = tips;
    };
    // 获取添加对象
    const addAttribute = (addObj) => {
      data.openDetails = false;
      data.objList.push(addObj);
    };
    // 生成数据
    const generateData = () => {
      let simulationObj = {};
      let value = '';
      data.objList.forEach((item) => {
        if (item.type === 'number') {
          value = Math.round(Math.random() * 30 + 20);
        } else {
          value = randomString('SCHOOL_NAME');
        }
        simulationObj[item.name] = `'${value}'`; //值加单引号
      });
      data.json.push(simulationObj);
      let originalData = JSON.stringify(data.json, null, 4); //格式化
      data.outputData = originalData.replace(/"/g, ''); //去除双引号
    };
    return {
      clickDetails,
      generateData,
      addAttribute,
      allocation,
      ...toRefs(data)
    };
  }
};
</script>
