<template>
  <div class="set-details-box">
    <div class="set-details-item">
      <!-- 属性名称 -->
      <div class="details-input-item">
        <p class="input-name">属性名称 :</p>
        <input class="input-ele" type="text" v-model="typeName" />
      </div>
      <!-- 属性类型 -->
      <div class="details-input-item">
        <p class="input-name">属性类型 :</p>
        <p
          class="type-item"
          v-for="item in navList"
          :key="item.type"
          :class="{ active: typeItem === item.type }"
          @click="typeItem = item.type"
        >
          {{ item.type }}
        </p>
      </div>
      <div class="details-input-box"></div>
    </div>
    <p @click="addAttribute" class="add-attribute">添加</p>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
export default {
  props:{
    aTypeTips:{
      type:String,
    }
  },
  name: '',
  setup(props, ctx) {
    const data = reactive({
      typeName: '',
      typeItem: 'number',
      navList: [
        { type: 'number' },
        { type: 'string' },
        { type: 'null' },
        { type: 'undefined' }
      ]
    });
    const addAttribute = () => {
      if (data.typeName) {
        const attribute = {
          name: data.typeName,
          type: data.typeItem
        };
        ctx.emit('addAttribute', attribute);
      } else {
        console.log('添加属性名称');
      }
    };
    return {
      addAttribute,
      ...toRefs(data)
    };
  }
};
</script>
